<template>
  <div class="home">
    <div class="bg-red h-100 w-100 d-flex align-items-center justify-content-center">
      <button @click="gotoGame">play</button>
    </div>
    <transition name="fade">
      <loading v-if="isLoading"></loading>
    </transition>
  </div>
</template>

<script>
// import $ from 'jquery'
import Loading from '../components/Loading'
export default {
  name: 'Bet',
  components: {
    Loading
  },
  data () {
    return {
      isLoading: false
    }
  },
  created () {
  },
  mounted () {
  },
  methods: {
    gotoGame () {
      this.$router.push({name: 'Game'})
    },
    gotoRecord () {
      this.$router.push({name: 'Record'})
    },
    gotoReward () {
      this.$router.push({name: 'Reward'})
    },
    gotoRule () {
      this.$router.push({name: 'Rule'})
    }
  }
}
</script>

<style>
</style>



