<template>
  <div class="game d-flex align-items-center">
    <div>
      <div class="text-white h8 text-center mb-2">Time: {{time}} Score: {{score}} version2</div>
      <div class="game-content d-flex flex-wrap w-85 mx-auto mb-3">
        <div v-for="(items,index) in 9" :key="index" class="game-items m-2 bg-white position-relative">
          <transition name="fade">
            <div :id="`game_item_${index}`" class="d-none position-absolute game-click-item" @click="getScore(index)">
              <img src="../assets/img/u-money.png" width="100%" alt="">
            </div>
          </transition>
          <div class="">
            <img src="../assets/img/u-money2.png" width="100%" alt="">
          </div>
          <!-- <div>o</div> -->
        </div>
      </div>
      <div v-if="!playing" class="d-flex justify-content-center mb-3">
        <div class="text-white">
          <input type="radio" id="easy" value="easy" v-model="level">
          <label for="easy">easy</label>
          <input type="radio" id="normal" value="normal" v-model="level">
          <label for="normal">normal</label>
          <input type="radio" id="difficult" value="difficult" v-model="level">
          <label for="difficult">difficult</label>
        </div>
      </div>
      <div class="start-btn p-3 text-white mx-auto" @click="start">start</div>
    </div>
    <div class="model d-flex align-items-center justify-content-center" v-if="finished">
      <div>
        <div class="text-white h8 text-center mb-5">Your Score : {{score}}</div>
        <div class="start-btn p-3 text-white mx-auto" @click="gamefinish">close</div>
      </div>
    </div>
    <div class="model d-flex align-items-center justify-content-center" v-if="count">
      <div>
        <div class="text-white h8 text-center mb-5">{{counttime}}</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'game',
  data () {
    return {
      score: 0,
      timer: null,
      timer2: null,
      alltimer: null,
      countdown: null,
      numarr: [],
      time: 30,
      playing: false,
      finished: false,
      level: 'normal',
      disappearspeed: 500,
      addspeed: 400,
      countplay: null,
      counttime: 3,
      counttimetimer: null,
      count: false
    }
  },
  created () {
    this.newarray()
  },
  mounted () {
  },
  watch: {
    time () {
      if (this.time === 0) {
        clearInterval(this.countdown)
        clearInterval(this.timer)
        clearTimeout(this.timer2)
        console.log(this.time)
        document.querySelectorAll('.game-click-item').forEach(item => {
          item.classList.add('d-none')
        })
        this.alltimer = setTimeout(() => {
          this.finished = true
        }, 1000)
      }
    },
    level () {
      if (this.playing) return
      if (this.level === 'easy') {
        this.disappearspeed = 1000
        this.addspeed = 500
      } else if (this.level === 'normal') {
        this.disappearspeed = 500
        this.addspeed = 400
      } else if (this.level === 'difficult') {
        this.disappearspeed = 200
        this.addspeed = 300
      }
    }
  },
  methods: {
    getScore (index) {
      // console.log(this, document.)
      this.score += 1
      document.querySelector(`#game_item_${index}`).classList.add('d-none')
      console.log(this.score)
    },
    gamefinish () {
      this.playing = false
      this.finished = false
      this.score = 0
    },
    newarray () {
      let arr = [0, 1, 2, 3, 4, 5, 6, 7, 8]
      for (let i = 0; i < 9; i++) {
        let num = Math.floor(Math.random() * (9 - i))
        arr.splice(0, 1)
        console.log(num, arr)
        // console.log(arr[num])
      }
      // for (let i = 0; i < 100; i++) {
      //   this.numarr.push(Math.floor(Math.random() * 9))
      //   if (this.numarr[i] === this.numarr[i - 1]) {
      //     console.log('i-1', i, this.numarr[i])
      //     this.numarr[i] = Math.floor(Math.random() * 9)
      //   }
      // }
      // console.log(this.numarr)
    },
    start () {
      if (this.playing) return
      this.count = true
      this.playing = true
      this.time = 30
      this.counttime = 3
      this.counttimetimer = setInterval(() => {
        this.counttime -= 1
        if (this.counttime === 0) {
          this.count = false
        }
      }, 1000)
      this.countplay = setTimeout(() => {
        clearInterval(this.counttimetimer)
        this.countdown = setInterval(() => {
          this.time -= 1
        }, 1000)
        let i = 0
        this.timer = setInterval(() => {
          let j = i
          document.querySelector(`#game_item_${this.numarr[i]}`).classList.remove('d-none')
          this.timer2 = setTimeout(() => {
            document.querySelector(`#game_item_${this.numarr[j]}`).classList.add('d-none')
          }, this.disappearspeed)
          i++
        }, this.addspeed)
      }, 3500)
    }
  }
}
</script>

<style>
.game{
  height: 100vh;
  min-height: 100%;
  background-color: rgb(169, 216, 243);
}
.w-85{
  width: 85%;
}
.game-items{
  width: calc((100% / 3) - 1rem) ;
}
@keyframes move {
  0% {
    transform: translateY(5px);
  }
  50% {
    transform: translateY(2px);
  }
  100% {
    transform: translateY(0px);
  }
}
.game-click-item{
  animation: move 1s ease-in;
  /* z-index: 999; */
}
.start-btn{
  border: 1px solid #ffffff;
  width: fit-content;
}
.model{
  height: 100vh;
  width: 100%;
  min-height: 100%;
  background-color: rgba(66, 66, 66, 0.575);
  position: absolute;
  top: 0;
  left: 0;
}
</style>



